<template>
  <div>
    <el-row class="demo-autocomplete">
      <el-col :span="12">
        <el-autocomplete
          class="inline-input"
          v-model="otime"
          :fetch-suggestions="querySearch1"
          placeholder="请选择时间"
        ></el-autocomplete>
      </el-col>
      <el-button @click="download">下载</el-button>
    </el-row>
  </div>
</template>
<style>
.web {
  height: 100%;
  position: relative;
  z-index: -6;
}
</style>
<script>
import axios from "axios";
import { Toast } from "vant";
export default {
  data() {
    return {
      restaurants1: [],
      otime: ""
    };
  },
  methods: {
    querySearch1(queryString, cb) {
      var restaurants = this.restaurants1;
      var results = queryString
        ? restaurants.filter(this.createFilter1(queryString))
        : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter1(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    download() {
      if (this.otime == "") {
        Toast("请选择时间");
        return;
      } else {
        window.location.href = "http://localhost:3000/" + this.otime + ".xlsx";
        // window.location.href = "http://tongji.oovovv.cn/" + this.otime + ".xlsx";
      }
    }
  },
  mounted() {
    let that = this;
    axios.post("http://localhost:3000/outtime").then(res => {
      res.data.forEach(element => {
        that.restaurants1.push({ value: element.time });
      });
    });
  },
  beforeCreate: function() {
    document.getElementsByTagName("body")[0].className = "web";
  }
};
</script>