    pass(a) {
      Dialog.confirm({
        title: "提示",
        message: "是否确认通过？"
      })
        .then(() => {
          // on confirm
          axios
            .post(this.address + "pass", {
              _id: a
            })
            .then(res => {
              if (res.data !== "no") {
                this.Ysall();
                Toast("通过成功～");
              }
              if (res.data == "no") {
