    searchSettlement() {
      axios
        .post(this.address + "searchSettlement", {
          name: this.sname1,
          card: this.scard1,
          number: this.snumber1,
          ls: this.lsname1,
          state: this.state
        })
        .then(res => {
          this.total2 = 0;
          this.passarr = res.data;
        });
    },

    //input模糊查询
    querySearch(queryString, cb) {
      var restaurants = this.restaurants;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    querySearch1(queryString, cb) {
      var restaurants = this.restaurants1;
      var results = queryString
        ? restaurants.filter(this.createFilter1(queryString))
        : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter1(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    //
    change(a) {
      this.Settlementid = a;
      console.log(this.Settlementid);
      // axios.post(this.address + "Settlement", { _id: a }).then(res => {
      //   console.log(res);
      // });
    },
    close1(a, b) {
      if (a == "cancel") {
        b(true);
      } else {
        if (this.money !== "") {
          b();
        }
        if (this.money == "") {
          b(false);
        }
      }
    },
    close2(a, b) {
      if (a == "cancel") {
        b(true);
      } else {
        if (this.money1 !== "") {
          b();
        }
        if (this.money1 == "") {
          b(false);
        }
      }
    },
    close3(a, b) {
      if (a == "cancel") {
        b(true);
      } else {
        if (this.money2 !== "") {
          b();
        }
        if (this.money2 == "") {
          b(false);
        }
      }
    },
    Settlement() {
      if (this.money == "") {
        this.errmoney = true;
        return;
      }
      var date = new Date(); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
      var Y = date.getFullYear();
      var M =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      var D = date.getDate();
      var h = date.getHours();
      var m = date.getMinutes();
      if (D - 10 < 0) {
        D = "0" + D;
      }
      if (h - 10 < 0) {
        h = "0" + h;
      }
      if (m - 10 < 0) {
        m = "0" + m;
      }
      var b = Y + "-" + M + "-" + D + " " + h + "时" + m + "分";
      axios
        .post(this.address + "Settlement", {
          _id: this.Settlementid,
          money: this.money,
          endtime: b
        })
        .then(res => {
          console.log(res.data);
          this.sallpass();
          this.Ysallpass();
        });
    },
    Settlement2() {
      if (this.money2 == "") {
        this.errmoney = true;
        return;
      }
      axios
        .post(this.address + "Settlement2", {
          _id: this.Settlementid,
          money: this.money2
        })
        .then(res => {
          console.log(res.data);
          this.sallpass();
          this.Ysallpass();
        });
    },
    //一键结算
    Settlement1() {
      if (this.chooseid1.length == 0) {
        Toast("请勾选数据");
        return;
      }
      if (this.money1 == "") {
        this.errmoney1 = true;
        return;
      }
      var date = new Date(); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
      var Y = date.getFullYear();
      var M =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      var D = date.getDate();
      var h = date.getHours();
      var m = date.getMinutes();
      if (D - 10 < 0) {
        D = "0" + D;
      }
      if (h - 10 < 0) {
        h = "0" + h;
      }
      if (m - 10 < 0) {
        m = "0" + m;
      }
      var b = Y + "-" + M + "-" + D + " " + h + "时" + m + "分";
      axios
        .post(this.address + "Settlement1", {
          id: this.chooseid1,
          money: this.money1,
          endtime: b
        })
        .then(res => {
          if (res.data == "no") {
            Toast("操作失败");
          } else {
            this.sallpass();
            this.Ysallpass();
            Toast("成功");
          }
        });
