    //禁用登陆
    banstudent(a) {
      console.log(a);
      axios.post(this.address + "banstudent", { _id: a._id }).then(res => {
        console.log(res);
        if (res.data == "ok") {
          this.findstudent();
          Toast("成功");
        }
      });
    },
    //修改组长
    capid(a) {
