          if (res.data == "ok") {
            this.findstudent();
            this.findteacherall();
            Toast("修改成功");
          } else {
            Toast("修改失败");
          }
        });
    },
    //导出
    outanddown() {
      // this.fullscreenLoading = true;
      const loading = this.$loading({
        lock: true,
        text: "Loading",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
      });
      let date = this.value[0];
      let date1 = this.value[1];
      axios
        .post(this.address + "outanddown", {
          name: this.sname,
          card: this.scard,
          number: this.snumber,
          date: date,
          date1: date1,
          ls: this.lsname,
          state: this.state,
          class: this.sname2
        })
        .then(res => {
          console.log(res.data);
          this.fullscreenLoading = true;
          if (res.data !== "no") {
            this.fullscreenLoading = false;
            // window.location.href =
            //   "http://localhost:3000/" + date + "-" + date1 + ".xlsx";
            window.location.href =
              "http://tongji.oovovv.cn/" + date + "-" + date1 + ".xlsx";
            loading.close();
            setTimeout(() => {
              axios
                .post(this.address + "delefile", {
                  date: date,
                  date1: date1
                })
                .then(res => {
                  console.log(res.data);
                });
            }, 500);
          } else {
            Toast("数据为空，无法导出～");
          }
        });
    },
    getTime() {
      // let flag = 17;
      var date = new Date(); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
      var Y = date.getFullYear();
      var M =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      var D = date.getDate();
      if (D - 10 < 0) {
        D = "0" + D;
      }
      var a = Y + "-" + M + "-" + D;
      this.outtime = a;
    },
    outanddown1() {
      this.getTime();
      axios
        .post(this.address + "outanddown1", { time: this.outtime })
        .then(res => {
          if (res.data !== "no") {
            window.location.href =
              "http://tongji.oovovv.cn/" + this.outtime + ".xlsx";
            // window.location.href =
            //   "http://localhost:3000/" + this.outtime + ".xlsx";
          }
          if (res.data == "no") {
            Toast("数据为空，无法导出～");
          }
        });
    },
    //工具
    sall() {
      axios.post(this.address + "sall").then(res => {
        this.total1 = Math.ceil(res.data.length / 30) * 10;
      });
    },
    sallpass() {
      axios.post(this.address + "sallpass").then(res => {
        this.total2 = Math.ceil(res.data.length / 30) * 10;
      });
    },
    getls() {
      let that = this;
      this.ls = [{ name: "选择银行" }];
      this.ls1 = [];
      axios.post(this.address + "getls").then(res => {
        res.data.forEach(element => {
          that.ls.push(element);
          that.ls1.push(element);
        });
      });
    },
    findstudent() {
      axios.post(this.address + "findstudent").then(res => {
        this.total3 = Math.ceil(res.data.length / 30) * 10;
      });
    },
    findteacherall() {
      let that = this;
      axios.post(this.address + "findteacher").then(res => {
        this.team = res.data;
        res.data.forEach(element => {
          that.restaurants.push({ value: element.name });
        });
      });
    },
    getdown() {
      let that = this;
      axios.post(this.address + "outtime").then(res => {
        res.data.forEach(element => {
          that.restaurants1.push({ value: element.time });
        });
      });
    },
    //优化显示
    changecurrent1(a) {
      this.currentpage1 = a;
      this.Ysall();
    },
    changecurrent2(a) {
      this.currentpage2 = a;
      this.Ysallpass();
    },
    changecurrent3(a) {
      this.currentpage3 = a;
      this.Yfindstudent();
    },
    Ysall() {
      axios
        .post(this.address + "Ysall", {
          page: this.currentpage1 - 1
        })
        .then(res => {
          this.arr = res.data;
        });
    },
    Ysallpass() {
      axios
        .post(this.address + "Ysallpass", { page: this.currentpage2 - 1 })
        .then(res => {
          this.passarr = res.data;
        });
    },
    Yfindstudent() {
      axios
        .post(this.address + "Yfindstudent", { page: this.currentpage3 - 1 })
        .then(res => {
          this.student = res.data;
        });
    },
    //下载
    download() {
      if (this.otime == "") {
        Toast("请选择时间");
        return;
      } else {
        // window.location.href = "http://localhost:3000/" + this.otime + ".xlsx";
        window.location.href =
          "http://tongji.oovovv.cn/" + this.otime + ".xlsx";
      }
    }
  },

  mounted() {},
  created() {
    this.sall();
    this.Ysall();
    this.sallpass();
    this.Ysallpass();
    this.getls();
    this.findstudent();
    this.Yfindstudent();
    this.findteacherall();
    this.getdown();
  },
  beforeCreate: function() {
    document.getElementsByTagName("body")[0].className = "web";
  }
};
</script>